export default{
  props: {
    form: Object
  },
  data(){
    return{
      formLabelWidth: '100px',
      options: [{
        value: 'menu',
        label: 'menu'
      }, {
        value: 'button',
        label: 'button'
      }
      ],
      rules:{
        resZhCnName:[
          { required: true, message: this.$t('commom.required'), trigger: 'blur' },
        ],
        resEnUsName:[
          { required: true, message: this.$t('commom.required'), trigger: 'blur' },
        ],
        resKoKrName:[
          { required: true, message: this.$t('commom.required'), trigger: 'blur' },
        ],
        resJaJpName:[
          { required: true, message: this.$t('commom.required'), trigger: 'blur' },
        ],
        identity:[
          { required: true, message: this.$t('commom.required'), trigger: 'blur' },
        ],
        permission:[
          { required: true, message: this.$t('commom.required'), trigger: 'blur' },
        ],
      }
    }
  },
  methods:{
    clearParent(){
      this.form.parentId = 0;
      this.form.parentIds = "0";
      this.form.parentName = ""
    },
    validate(fn){
      this.$refs.form.validate((valid) => {
        if(valid){
            fn(valid)
        }else{

            return false
        }
      });
    },
    resetFields(){
      this.$refs.form.resetFields();
    }
  }
}



